body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --ck-z-default: 100;
  --ck-z-modal: calc(var(--ck-z-default) + 999);
}

p {
  text-align: left;
}

h1 {
  font-size: 34px;
  text-align: left;
}

h2 {
  font-size: 24px;
  text-align: left;
}

#root {
  /*background: url("../img/w17.jpg") no-repeat center center fixed;*/
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-family: LatoReg;
  font-size: 17px;
  letter-spacing: 0.01rem;
  line-height: 1.65;
}
#switch {
  display: none;
}

#Label,
.toggle {
  height: 1rem;
  border-radius: 100px;
}
#Label {
  position: relative;
  top: -30px;
  left: 5px;
  min-width: 100px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 100px;
  margin: 0;
  cursor: pointer;
}
.toggle {
  position: absolute;
  width: 50%;
  background-color: #fff;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.names {
  font-size: 10px;
  font-weight: bolder;
  width: 65%;
  margin-left: 17.5%;
  margin-top: 0.5%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  user-select: none;
}
.dark {
  opacity: 0.5;
}

