@keyframes fontSizeReverse {
  from {
    font-size: 2vw;
  }
  to {
    font-size: 1vw;
  }
}

@keyframes neon2 {
  to {
    background: linear-gradient(orange, yellow);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0 0 30px rgba(255, 248, 0, 1), 0 0 35px rgba(255, 248, 0, 1),
      0 0 40px rgba(255, 248, 0, 1), 0 0 45px rgba(255, 248, 0, 1),
      0 0 50px rgba(255, 248, 0, 1), 0 0 55px rgba(255, 248, 0, 1),
      0 0 60px rgba(255, 248, 0, 1), 0 0 70px rgba(255, 248, 0, 1);
  }
}

